import { createRouter, createWebHistory } from "vue-router";
import Header from "../views/HeaderTitle.vue";


var routes = [];


if(process.env.NODE_ENV === 'production'){
  routes.push({ path:'/', redirect: {name: 'Home'} })
  routes.push({ path:'/fbb/', name: 'Header', component: Header })
}else{
  routes.push({ path: "/", name: "Header", component: Header, })
}

// var routes = [
//   { path: "/", name: "Header", component: Header, }
// ];




const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});
export default router;
